/* Container styling */
.car-list-container {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  /* Error message */
  .error-message {
    color: red;
    margin-bottom: 15px;
  }
  
  /* Table styling */
  .car-list-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    text-align: left;
  }
  
  .car-list-table th,
  .car-list-table td {
    padding: 10px;
    border: 1px solid #ddd;
  }
  
  .car-list-table th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  
  .row-white {
    background-color: #fff;
  }
  
  .row-gray {
    background-color: #f9f9f9;
  }
  
  /* Action buttons */
  .edit-button,
  .delete-button {
    margin-right: 5px;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-size: 14px;
  }
  
  .edit-button {
    background-color: #007bff;
  }
  
  .edit-button:hover {
    background-color: #0056b3;
  }
  
  .delete-button {
    background-color: #dc3545;
  }
  
  .delete-button:hover {
    background-color: #a71d2a;
  }
  